import React, { useEffect, useState } from 'react'
import './About.css'
import Mailto from 'react-protected-mailto'


export default function About() {
    const [isCreditsClicked, setIsCreditsClicked] = useState(false);
    const [windowWidth, setWindowWidth] = useState(window.innerWidth)
    const [windowHeight, setWindowHeight] = useState(window.innerHeight)

    const handleResize = () => {
        setWindowWidth(windowWidth => window.innerWidth)
        setWindowHeight(windowHeight => window.innerHeight)
    }

    useEffect(() => {
        window.addEventListener('resize', handleResize)

        return () => {
            window.removeEventListener('resize', handleResize)
        }
    }, [windowWidth, windowHeight])


return (
    <div className="About">
        {/* <p>FR | EN</p> */}
        <div className="credits" >
            <p className="a" onClick={() => setIsCreditsClicked(!isCreditsClicked)}>Credits </p>
            <p className={"credits-text" + (isCreditsClicked ? "show" : "hide")}>
                <br />Typefaces: <a href="http://collletttivo.it/">Apfel Grotezk</a> by Luigi Gorlero, <a href="http://velvetyne.fr/fonts/lack/">Lack</a> by Adrien Midzic.
                    <br /><br />Video: <a href="https://videezy.com">Videezy</a>.
                    <br /><br />3D Models based on <a href="https://www.turbosquid.com/3d-models/free-max-mode-computer/161485">Network</a> by <a href="https://www.turbosquid.com/Search/Artists/graphixkid44">graphixkid44</a>, <a href="https://skfb.ly/6XnUu">Floppy Disk</a> by <a href="https://sketchfab.com/Nyangire">Nyangire</a>, <a href="https://skfb.ly/6zvYJ">Very Simple CD- Disc</a> by <a href="https://sketchfab.com/Blender3D">Blender3D</a>, <a href="https://skfb.ly/6YyrA">Rose</a> by <a href="https://sketchfab.com/greenlive">greenlive</a>.
                </p>
        </div>

        <p className="contact">
            Contact me: <br />
            <Mailto className="a"
                email='nai-tr@pm.me'
                headers={{ subject: 'Question from the website' }} />
        </p>

        <p className={"bio"+ ((isCreditsClicked && (windowWidth < 600 || windowHeight < 500)) ? "hide" : "")} >
            Hi, my name is Naiara. I am a student in web development currently living in Paris. Deeply passionate about technology and computer generated imagery, I like to explore the creative possibilities of the web and the uniqueness of the worlds and spaces that this medium brings into life.
            </p>

    </div>
)
}




