import './CanvasContainer.css'
import { Canvas } from '@react-three/fiber'
import ModelComputer from './ModelComputerLow'
import ModelFloppyCD from './ModelFloppyCDLow'
import ModelRose from './ModelRoseLow'
import { Environment, ContactShadows, softShadows, MapControls, Plane, TransformControls } from "@react-three/drei";
import { Suspense, useState, useEffect, useRef } from "react";

softShadows()

export default function CanvasContainer() {

    const [camera, setCamera] = useState({ fov: 12, near: 0.1, far: 1000, position: [8, 4, 12], zoom: 1.8});
    const ref = useRef();
    const [zoom, setZoom] = useState(1)
    const [windowWidth, setWindowWidth] = useState(window.innerWidth)

    const handleResize = () => {
        setWindowWidth(windowWidth => window.innerWidth)
    }

    const handleZoom = () => {
        //linear function 0.000739372 * x +0.664325
        const a =  0.000739372;
        const b = 0.664325;
        setZoom(a * windowWidth + b); 
        setCamera(prevCamera => {return {...prevCamera, zoom: zoom }});
    }

    useEffect(() => {

        window.addEventListener('resize', handleResize)
        handleZoom()
        // console.log(ref.current)
       

        return() => {
            window.removeEventListener('resize', handleResize)
        }
    }, [windowWidth, zoom, ref])

    

    return (
        <div className="CanvasContainer">
            {/* <Canvas className="Canvas" shadows camera={{ fov: 12, near: 0.1, far: 1000, position: [5, 3, 8] }}> */}
                <Canvas ref={ref} className="Canvas" shadows="true" camera={camera}>
                <Suspense fallback={null}>
                    <ModelComputer position={[-0.14, -0.3, 0]} castShadow revieveShadow />
                    <ModelFloppyCD position={[-0.9, -0.25, 0.1]} castShadow recieveShadow />
                    <ModelRose position={[-0.4, -0.15, 0.7]} rotation-y={0.5} rotation-z={0.2} castShadow recieveShadow />
                    <directionalLight
                        intensity={0.5}
                        castShadow
                        shadow-mapSize-height={512}
                        shadow-mapSize-width={512}
                    />
                    <pointLight position={[2, 2, 2]} intensity={0.5} castShadow shadow-mapSize-width={1024}
                        shadow-mapSize-height={104}
                        shadow-camera-far={50}
                        shadow-camera-left={-10}
                        shadow-camera-right={10}
                        shadow-camera-top={10}
                        shadow-camera-bottom={-10} />
                    <Environment preset="sunset" />
                    <ContactShadows rotation-x={Math.PI / 2} position={[0, -0.245, 0]} opacity={0.4} width={5} height={5} blur={1} far={0.4} />
                    <MapControls/>
                </Suspense>
            </Canvas>
        </div>
    )
}
